body{
  background-color: rgb(88, 88, 92);
}
.App{
  background-color: rgb(100, 161, 231);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
#to-do-form input{
  background-color: rgb(189, 195, 216);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder{
  color: black;
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  background-color: rgb(187, 224, 117);
  cursor: pointer;
  font-weight: bolder;
}